// Routes.js
import React from 'react';
import { Routes, Route } from 'react-router-dom';

import GKCite from './pages/gkcite';
import Cite from './pages/cite';
import QueryRouter from './pages/queryRouter';
import ipLookUp from './utils/getstats';

const Main = () => {
  ipLookUp();
  return (
    <Routes> {/* The Switch decides which component to show based on the current URL.*/}
      <Route exact path='/' element={<QueryRouter/>}></Route>
      <Route exact path='/cite/:id' element={<Cite/>}></Route>
    </Routes>
  );
}

export default Main;