// src/components/AddParagraphModal.js
import React, { useState, useRef, useEffect, forwardRef, useImperativeHandle } from 'react'; // ADD useEffect
import Modal from 'react-modal';
import { Button, TextField, Flex } from '@aws-amplify/ui-react';

Modal.setAppElement('#root');

const AddParagraphModal = forwardRef(({ onClose, onAdd, editingParagraph, category }, ref) => {
  const [text, setText] = useState(editingParagraph ? editingParagraph.Text : '');
  const [sourceLink, setSourceLink] = useState(editingParagraph ? editingParagraph.SourceLink : '');
  const [tags, setTags] = useState(editingParagraph ? editingParagraph.Tags : '');

  const internalTextAreaRef = useRef(null);

  useImperativeHandle(ref, () => ({
    focusTextArea: () => {
      if (internalTextAreaRef.current) {
        internalTextAreaRef.current.focus();
        internalTextAreaRef.current.setSelectionRange(text.length, text.length);
      }
    }
  }));

  const addOrEditParagraph = async () => {
    const isEditing = !!editingParagraph;
    const url = `https://l1az6ozvxa.execute-api.us-east-1.amazonaws.com/alpha/gkcite/api`;
    const method = isEditing ? 'PUT' : 'POST';

    const currentCategory = (editingParagraph && editingParagraph.Category) || category;
    const body = JSON.stringify(isEditing ? { paragraph_id: editingParagraph.ParagraphId, text, source_link: sourceLink, tags, category: currentCategory } : { text, source_link: sourceLink, tags, category: currentCategory });

    try {
      const response = await fetch(url, {
        method: method,
        headers: {
          "Content-Type": "application/json",
          "Authorization": `${btoa("123456")}:${btoa(localStorage.getItem("password"))}`
        },
        body: body,
      });

      if (!response.ok) {
        throw new Error(`Error adding paragraph. Status: ${response.status}`);
      }

      const newParagraph = await response.json();
      console.log('New paragraph added:', newParagraph);
      onAdd(); // <--- This will trigger the parent's onAdd function
    } catch (error) {
      console.error('Error adding paragraph:', error);
    }
  };

  // NEW useEffect for keyboard shortcut
  useEffect(() => {
    const handleKeyDown = (event) => {
      // Check for Ctrl + Enter
      if (event.ctrlKey && event.key === 'Enter') {
        event.preventDefault(); // Prevent new line in textarea
        addOrEditParagraph();
      }
    };

    // Add event listener to the document when the modal is open
    document.addEventListener('keydown', handleKeyDown);

    // Clean up the event listener when the modal closes or component unmounts
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [text, sourceLink, tags, category, editingParagraph]); // Dependencies to ensure `addOrEditParagraph` has latest state values.

  const customStyles = {
    content: {
      padding: '20px',
      borderRadius: '4px',
      overflow: 'visible',
      maxWidth: '1800px',
      width: '90%',
      position: 'absolute',
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.75)'
    }
  };

  return (
    <Modal
      isOpen
      onRequestClose={onClose}
      style={customStyles}
      class="ReactModal__Content"
    >
      <Button
        onClick={onClose}
        style={{
          position: 'absolute',
          top: '10px',
          right: '10px',
          padding: '0',
          lineHeight: '1'
        }}
        ariaLabel="Close modal"
      >
        &times;
      </Button>

      <h2>Add Paragraph</h2>

      <Flex direction="column" gap="1rem">
        <TextField
          label="Source Link"
          placeholder="Enter source link"
          value={sourceLink}
          onChange={(e) => setSourceLink(e.target.value)}
        />
        <TextField
          as="textarea"
          label="Text"
          placeholder="Enter paragraph text"
          value={text}
          onChange={(e) => setText(e.target.value)}
          rows={4}
          style={{
            height: '50vh'
          }}
          ref={internalTextAreaRef}
        />
        <TextField
          label="Tags"
          placeholder="Enter tags (comma-separated)"
          value={tags}
          onChange={(e) => setTags(e.target.value)}
        />
        <Button
          onClick={addOrEditParagraph}
          variation="primary"
          isFullWidth={true}
        >
          {editingParagraph ? 'Update Paragraph' : 'Add Paragraph'}
        </Button>
      </Flex>
    </Modal>
  );
});

export default AddParagraphModal;