// cite.js
import React, { useState, useEffect, useRef } from 'react';
import AddParagraphModal from '../components/AddParagraphModal';
// import FilterParagraphs from '../components/FilterParagraphs'; // Can remove if unused
import ParagraphList from '../components/ParagraphList';
import { View, Button, Flex, Heading, Divider } from '@aws-amplify/ui-react';
import '@aws-amplify/ui-react/styles.css';
// import { useParams } from 'react-router'; // Can remove if unused
// import { useLocation } from 'react-router-dom'; // Can remove if unused

const Cite = () => {
  const [paragraph, setParagraph] = useState([]);
  const [isAddModalOpen, setAddModalOpen] = useState(false);
  const [editingParagraph, setEditingParagraph] = useState(null);
  const isReadOnly = localStorage.getItem("password") == null;

  const queryParams = new URLSearchParams(window.location.search);
  const id = queryParams.get('c');

  const addParagraphModalRef = useRef(null);

  const handleEdit = (paragraphToEdit) => {
    setEditingParagraph(paragraphToEdit);
    setAddModalOpen(true);
  };

  const openAddModal = () => {
    setEditingParagraph(null);
    setAddModalOpen(true);
  };

  const handleModalClose = () => {
    setAddModalOpen(false);
    setEditingParagraph(null); // Clear editingParagraph when closing modal
  };

  // NEW: Handle successful add/edit and close window
  const handleParagraphAdded = () => {
    handleModalClose(); // Close the modal
    if (window.opener) { // Check if the window was opened by another window
      window.close(); // Close the current window/tab
    } else {
      // Fallback for cases where window.opener is null (e.g., direct navigation)
      // You might want to navigate to a different page or show a message.
      console.log("Window cannot be closed directly as it wasn't opened by another script.");
      // history.push('/some-other-page'); // Example: if using react-router-dom
    }
  };

  const fetchParagraph = async (paragraphId) => {
    try {
      const response = await fetch(`https://l1az6ozvxa.execute-api.us-east-1.amazonaws.com/alpha/gkcite/api/paragraph/${paragraphId}`, {
        method: 'GET',
        headers: {
          "Content-Type": "application/json",
          "Authorization": `${btoa("123456")}:${btoa(localStorage.getItem("password"))}`
        }
      });
      if (!response.ok) {
        throw new Error(`Error fetching paragraphs. Status: ${response.status}`);
      }
      const paragraphData = await response.json();
      setParagraph(paragraphData);
    } catch (error) {
      console.error('Error fetching paragraphs:', error);
    }
  };

  useEffect(() => {
    fetchParagraph(id);
  }, [id]);

  useEffect(() => {
    const handleKeyPress = (event) => {
      // Press 'e' to open editor
      if (event.key === 'e' && !isAddModalOpen && paragraph && !isReadOnly) {
        event.preventDefault();
        handleEdit(paragraph);
      }
      
      // Press 'Escape' to close modal or tab
      if (event.key === 'Escape') {
        if (isAddModalOpen) {
          handleModalClose();
        } else {
          window.close();
        }
      }
    };

    document.addEventListener('keydown', handleKeyPress);
    return () => {
      document.removeEventListener('keydown', handleKeyPress);
    };
  }, [isAddModalOpen, paragraph, isReadOnly]);

  useEffect(() => {
    if (isAddModalOpen && addParagraphModalRef.current) {
      setTimeout(() => {
        addParagraphModalRef.current.focusTextArea();
      }, 0);
    }
  }, [isAddModalOpen, editingParagraph]);

  return (
    <View padding="1rem" className="citepage">
      {/* These script tags should typically be in your public/index.html or handled by a build process */}
      <script src="https://cdnjs.cloudflare.com/ajax/libs/react-modal/3.14.3/react-modal.min.js"
        integrity="sha512-MY2jfK3DBnVzdS2V8MXo5lRtr0mNRroUI9hoLVv2/yL3vrJTam3VzASuKQ96fLEpyYIT4a8o7YgtUs5lPjiLVQ=="
        crossOrigin="anonymous"
        referrerPolicy="no-referrer"></script>
      <script src="https://unpkg.com/packery@2.1/dist/packery.pkgd.js"></script>
      <Heading level={1}>GKCite</Heading>
      <Divider marginBottom="1rem" />

      <ParagraphList paragraphs={[paragraph]} onDelete={null} onEdit={handleEdit} isReadOnly={isReadOnly} />

      {isAddModalOpen && (
        <AddParagraphModal
          ref={addParagraphModalRef}
          onClose={handleModalClose}
          onAdd={handleParagraphAdded}
          visible={isAddModalOpen}
          editingParagraph={editingParagraph}
          category={editingParagraph?.Category || paragraph?.Category}
        />
      )}
    </View>
  );
};

export default Cite;